  .bg {
    background-size: cover;
    background-repeat: repeat;
    width: 100%;
    background: rgba(207, 186, 174, 0.62);
    z-index: 90;
  }

  .img:hover {
    cursor: pointer;
    
  }

  .gif {
    position: absolute;
    display: flex;
    background: rgba(207, 186, 174, 0.62);
  
    justify-content: center;
    align-content: center;
    width: 100%;
    height: 100%;
  }
  
  .gif img {
    display: flex;
    margin-top: 80px;
    width: 80px;
    height: 80px;
  }

  .mensaje {
    color:transparent
  }

  .mensaje:hover{
    color:rgba(31, 31, 30, 0.815);
    font-family: "Source Serif Pro", serif;
    font-size: 94px;
    
  }
  
  /************* NavBar ***************/
  .nav {
    display: flex;
    background: rgba(209, 196, 189, 0.69);
    border: 1px solid #8f8e8e;
    height: 50px;
    width: 100%;
    top: 0px;
    position: fixed;
    z-index: 50;
  }
  
  .nav div {
    margin: auto;
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    flex-wrap: nowrap;
    width: 100%;
    height: 100%;
    padding: 3px;
  }
  
  .nav img {
    border-radius: 15px;
    margin-left: 10px;
    margin-right: 10px;
    height: 45px;
    width: 60px;
  }
  
  .nav img:hover {
    cursor: pointer;
  }
  
  .nav button {
    border-radius: 15px;
    margin-left: 2px;
    margin-top: 5px;
    height: 35px;
    width: 120px;
    padding: 5px;
    text-align: center;
    border: 1px solid #8f8e8e;
  
    font-family: "DM Sans", sans-serif;
    /* background: rgba(209, 196, 189, 0.69); */
    background: rgba(178, 165, 157, 0.69);
    font-size: 20px;
    font-weight: bold;
  }
  
  .nav button:hover {
    cursor: pointer;
    transition: background-color 0.5s;
    background-color: white;
    color: black;
    font-weight: bold;
    border: 1px solid #313131;
  }
  
  /************* Posición Detalle ***************/
  .position {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: center;
    align-items: center;
  }
  
  /************* Fondo Detalle ***************/
  .bgDetail {
    width: 600px;
    height: 40%;
    display: flex;
    flex-wrap: wrap;
    /* flex-direction: column; */
    flex-direction: row;
    justify-content: center;
    align-items: center;
    /* text-align: justify; */
    /* overflow: hidden; */
  
    background-color: rgba(209, 196, 189, 0.69);
    border-radius: 20px;
    box-shadow: 5px 5px 15px 5px #000000;
    margin-top: 100px;
    margin-bottom: 20px;
    padding: 20px 0px;
  }
  
  /************* Título ***************/
  .bgDetail h1 {
    margin-top: 0px;
    text-align: center;
    font-family: "Source Serif Pro", serif;
  }
  
  /************* Contenedor 1 de Imagen - H3 - H4 ***************/
  .contenido {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    background-color: rgba(128, 128, 128, 0.253);
    width: 100%;
    border-radius: 20px;
  }
  
  .contenido img {
    display: flex;
    background-size: cover;
    background-repeat: repeat;
    border-radius: 20px;
    /* width: 400px;
    height: 300px; */
    min-width: 200px;
    min-height: 150px;
    max-width: 500px;
    max-height: 400px;
    border: 1px solid rgba(0, 0, 0, 0.45);
    margin: 8px;
    margin-left: 20px;
  }
  
  .contenido h3 {
    font-family: "DM Sans", sans-serif;
    display: flex;
    width: 170px;
    justify-content: flex-end;
    font-weight: bold;
    padding-right: 10px;
  }
  
  .contenido h4 {
    font-family: "DM Sans", sans-serif;
    display: flex;
    width: 180px;
    justify-content: flex-start;
    font-weight: lighter;
  }
  
  .contenido2 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-content: space-around;
    justify-content: space-evenly;
    width: 360px;
  }
  
  /************* Fondo Detalle 2 ***************/
  .bgDetail2 {
    background-color: rgba(209, 196, 189, 0.69);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: space-around;
    text-align: justify;
  
    width: 600px;
    height: 100%;
  
    margin-top: 20px;
    margin-bottom: 60px;
    border-radius: 20px;
    box-shadow: 5px 5px 15px 5px #000000;
  
    overflow: hidden;
    padding: 20px 0px;
  }
  
  /************* Contenedor 2 de Fondo - H3 - P ***************/
  .contenido3 {
    display: flex;
    background-color: rgba(128, 128, 128, 0.253);
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-radius: 20px;
  }
  
  .contenido3 h3 {
    font-family: "DM Sans", sans-serif;
    font-weight: bold;
  }
  
  .contenido3 p {
    font-family: "DM Sans", sans-serif;
  }

