    text-align: justify;
  
    width: auto;
    height: auto;
  
    margin-top: 20px;
    margin-bottom: 60px;
    border-radius: 20px;
    box-shadow: 5px 5px 15px 5px #000000;
  
    overflow: hidden;
    padding: 20px 0px;


  }

  .contenidoingredients {
    display:flex;
    background-color: rgba(128, 128, 128, 0.253);
    flex-direction: column;
    align-items: center;
    padding: 20px;
    border-radius: 20px;
    
  

